<template>
  <div>
      <h1>List</h1>
      <hr>
      <table v-if="todos.length">
        <thead>
          <tr>
            <th>№</th>
            <th>Title</th>
            <th>Items</th>
            <th>Controls</th>
          </tr>
        </thead>
        <tbody>
          <tr
            v-for="(todo, index) in todos"
            :key="todo.id"
          >
            <td>{{index + 1}}</td>
            <td>{{todo.title}}</td>
            <td>{{todo.itemsList.length}}</td>
            <td>
              <router-link tad="button" class="btn btn-small" :to="'/todo/' + todo.id">
                Open
              </router-link>
              <button type="button" class="btn btn-small red" style="margin-left: 15px;" @click="removeTodo(todo.id)">Del</button>
            </td>
          </tr>
        </tbody>
      </table>
      <p v-else>No todos</p>
  </div>
</template>

<script>
export default {
  computed: {
    todos() {
      return this.$store.getters.todos;
    }
  },
  methods: {
    removeTodo(id) {
      this.$store.dispatch('removeTodo', {
        id: id,
      });
    }
  }
}
</script>

